<template>
  <div class="buyNow">
    <div class="navtop">
      <van-nav-bar title="确认订单" left-arrow @click-left="onClickLeft" />
    </div>

    <!-- 选择地址 -->
    <div class="address">
      <div class="addressIcon">
        <van-icon name="location" color="#fff" size="25" />
      </div>

      <ul class="addressInfo">
        <li class="userInfo">
          <span class="name">{{currentAddress.name}}</span>
          <span class="phone">{{currentAddress.tel}}</span>
          <span class="default" v-if="currentAddress.isDefault">默认</span>
        </li>
        <li class="detail">{{currentAddress.address}}</li>
        <li class="text">收货不便时，可以选择暂存服务</li>
      </ul>

      <van-icon name="arrow" color="#999" size="20" @click="isAddress" />
    </div>

    <!-- 地址列表 -->
    <van-popup
      v-model="addressShow"
      round
      closeable
      close-icon="close"
      position="bottom"
    >
      <p class="title">选择地址</p>
      <van-address-list
        v-model="currentAddressId"
        :list="list"
        default-tag-text="默认"
        @add="onAdd"
        @select="selectAddress"
      />
    </van-popup>

    <!-- 订单 -->
    <div class="order">
      <div class="head">订单信息</div>
      <ul class="orderList">
        <Card
          v-for="(item, i) in orderData.cartInfo"
          :key="i"
          :orderObj="item"
        ></Card>
      </ul>
      <div class="select">
        <van-cell title="优惠卷" value="请选择" is-link @click="isCoupon" />
        <van-cell title="积分抵扣">
          <template #right-icon>
            <p>当前积分 <span>{{deduction}}</span></p>
            <van-checkbox v-model="checked" @change="isChecked" />
          </template>
        </van-cell>
        <van-cell title="快递费用" :value="postage==0?'免运费':'￥'+postage" />
        <van-field
          v-model="message"
          autosize
          label="备注"
          type="textarea"
          maxlength="100"
          placeholder="请输入备注信息"
          show-word-limit
        />
        <div class="price">
          <van-cell title="商品总价" :value="price" />
          <van-cell title="快递费" :value="postage==0?'免运费':'+￥'+postage" />
          <van-cell title="积分抵扣" :value="checked?'-￥'+deduction:0" />
        </div>
      </div>
    </div>

    <!-- 优惠卷弹框 -->
    <van-popup
      v-model="CouponShow"
      round
      closeable
      close-icon="close"
      position="bottom"
    >
      <p class="title">优惠卷</p>
      <div class="img">
        <img src="../assets/image/couponEmpty.png" />
        <p>暂无优惠卷可用</p>
      </div>
    </van-popup>

    <!-- 提交订单 -->
    <van-submit-bar :price="sumPrice*100" button-text="提交订单" @submit="placeOrder"/>
  </div>
</template>

<script>
import Card from "../components/GoodsCard"
import "../assets/style/buynow.less"
  export default {
    name:"buynow",
    components:{
      Card
    },
    data(){
      return{
        // id
        cartId:"",

        // 订单信息数据
        orderData:[],

        // 是否使用积分抵扣
        checked:false,

        //商品总价
        price:0,

        //订单总价
        sumPrice:0,

        //运费
        postage:0,

        //积分
        deduction:0,

        //是否显示地址列表
        addressShow:false,

        //是否显示优惠卷列表
        CouponShow:false,

        currentAddressId: '1',//当前地址的id

        //当前地址
        currentAddress:{},

        // 地址列表数据
        list: [],

        //备注信息
        message:'',

        //用户余额
        userMoney:"",

        //key
        orderKey:"",

        //地址id
        arId:0

      }
    },

    created(){
      //获取商品id
      this.cartId = String(this.$route.params.id)

      this.getOrderData()

      this.getAddressListData()

    },

    methods: {
      //返回上一级
      onClickLeft() {
        this.$router.back()
      },

      //显示地址列表
      isAddress(){
        this.addressShow = true
      },

      //显示优惠卷列表
      isCoupon(){
        this.CouponShow = true
      },

      // 新增地址
      onAdd() {
        this.$router.push("/address");
      },

      // 获取订单信息
      getOrderData(){
        let tokenString = this.$cookies.get("tokenString");

        if (!tokenString) {
          console.log("浏览器token不存在");
          return this.$router.push({ name: "Login" });
        }

        this.axios({
          method:"POST",
          url: this.baseUrl + "/order/confirm",
          data:{
            cartId: this.cartId
          },
          headers: {
            "Authori-zation": "Bearer " + tokenString,
          },
        }).then(res=>{
          // console.log(res);
          this.$toast.clear();

          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.status == 200) {
            //获取价格信息
            this.getPrice(res.data.data.orderKey , res.data.data.addressInfo.id)

            this.orderKey = res.data.data.orderKey

            this.orderData = res.data.data
            // console.log(this.orderData);
            this.deduction = Number(res.data.data.userInfo.integral).toFixed()

            this.userMoney = res.data.data.userInfo.now_money

            this.arId = res.data.data.addressInfo.id

          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);

        })
      },

      //获取价格信息
      getPrice(key , addressId){
        let tokenString = this.$cookies.get("tokenString");

        this.axios({
          method:"POST",
          url: this.baseUrl + "/order/computed/" + key,
          data:{
            addressId,
            couponId: 0,
            payType: "yue",
            shipping_type: 1,
            useIntegral: 0,
          },
          headers: {
            "Authori-zation": "Bearer " + tokenString,
          },
        }).then(res=>{

          if (res.data.status == 200) {

            this.sumPrice = res.data.data.result.pay_price
            this.postage = res.data.data.result.pay_postage
            this.price = res.data.data.result.total_price

          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);

        })
      },

      // 积分抵扣
      isChecked(){
        if (this.checked) {
          this.sumPrice = this.sumPrice - this.orderData.userInfo.integral
        }else{
          this.sumPrice = this.orderData.priceGroup.totalPrice
        }
      },

      //查询用户地址列表
      getAddressListData() {
        //获取token
        let tokenString = this.$cookies.get("tokenString");

        //判断token存不存在
        if (!tokenString) {
          console.log("浏览器token不存在");
          return this.$router.push({ name: "Login" });
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
        });

        this.axios({
          //请求方式
          method: "GET",
          url: this.baseUrl + "/address/list?page=1&limit=20",
          headers: {
            "Authori-zation": "Bearer " + tokenString,
          },
        })
          .then((res) => {
            this.$toast.clear();

            // 如果没登陆，就跳到登录页面
            if (res.data.status == 410000) {
              this.$toast({
                message: res.data.msg,
                forbidClick: true,
                duration: 3000,
              });
              return this.$router.push({ name: "Login" });
            }

            // 如果登陆，就处理数据
            if (res.data.status === 200) {
              this.list = []
              res.data.data.forEach((v,i) => {
                this.list.push({
                  id: v.id,
                  name: v.real_name,
                  tel: v.phone,
                  address: `${v.province}${v.city}${v.district}${v.detail}`,
                  isDefault: Boolean(v.is_default),
                });
                if (v.is_default) {
                  this.currentAddressId = v.id;
                  this.currentAddress = {...this.list[i]};
                }
              });
            } else {
              this.$toast({
                message: res.data.msg,
                forbidClick: true,
                duration: 3000,
              });
            }
          })
          .catch((err) => {
            this.$toast.clear();
            console.log("err ==> ", err);
          });
      },

      //切换地址
      selectAddress(item) {
        this.currentAddressId = item.id;
        this.currentAddress = {...item};
        this.addressShow = false;

      },

      //提交订单
      placeOrder(){
        if (this.userMoney < this.sumPrice) {

          this.$router.push({name:"Loser" , params:{key : this.orderKey , id : this.arId}})
        }
      }

    },
  }
</script>